<script>
export default {
  name: 'MyPage',
  data() {
    return {
      fromReportView: false
    }
  },
  created() {
    // 获取来源信息
    this.fromReportView = this.$route.query.fromReportView === 'true';
    console.log('MyPage created, fromReportView:', this.fromReportView);
  },
  methods: {
    goToHomePage() {
      // 根据来源返回相应的首页状态
      this.$router.push({
        path: '/home',
        query: { 
          showReportPage: this.fromReportView ? 'true' : 'false'
        }
      });
      console.log('从我的页面返回首页，目标状态:', this.fromReportView ? '查报告' : '去挂号');
    },
    
    goToEmergencyContacts() {
      // 跳转到紧急联系人页面
      this.$router.push({
        path: '/emergency-contacts',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToInsuranceCode() {
      // 跳转到医保码页面
      this.$router.push({
        path: '/insurance-code',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToBillDetail() {
      // 跳转到账单明细页面
      this.$router.push({
        path: '/bill-detail',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToEmergencyCall() {
      // 跳转到紧急呼救页面
      this.$router.push({
        path: '/emergency-call',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToMedicalRecord() {
