      this.$router.push({
        path: '/medical-record',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToSpeechRate() {
      // 跳转到语速调节页面
      this.$router.push({
        path: '/speech-rate',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    
    goToFontSize() {
